@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400&display=swap);
* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    line-height: 1.15;
    -webkit-text-size-adjust: 100%;
    font-family: 'Poppins', sans-serif;
}

body {
  margin: 0;
}

a {
  color: black;
  text-decoration: none;
}

.btn {
  display: inline-block;
  background: #000;
  color: #fff;
  border: none;
  padding: 10px 20px;
  margin: 5px;
  border-radius: 5px;
  cursor: pointer;
  text-decoration: none;
  font-size: 15px;
  font-family: inherit;
}

.btn:focus {
  outline: none;
}

.btn:active {
  transform: scale(0.98);
}




.App {
  text-align: center;
}


.Navlinks {
    display: flex;
    justify-content: space-around;
    align-items: center;
    background-color: rgb(255, 255, 255);
    height: 12vh;
    margin-bottom: 50px;
    border-bottom: 5px solid black;

}

.logo > strong {
    font-size: 32px;
}


.links {
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 20%;
    list-style: none;
    font-size: 20px;
    height: 100%;
}

.links > li {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    margin: 0 5px;
}

@media (max-width: 460px) {
    .links {
        font-size: 16px;
        margin-right: 10px;
    }

}



.Shop {
    display: flex;
    flex-flow: column;
    justify-content: center;
    align-items: center;
}

.shop-page-header {
    width: 90%;
    margin-bottom: 10px;
    text-align: left;
}

.products-container {
    display: flex;
    flex-flow: row wrap;
    justify-content: space-around;
    align-items: center;
}


.ShopItem {
    display: flex;
    flex-flow: column;
    justify-content: center;
    align-items: center;
    border: 1px solid black;
    border-radius: 5px;
    padding: 10px;
    width: 200px;
    height: 250px;
    margin: 10px;
}

.ShopItem p {
    color: rgb(43, 43, 43);
    margin-top: 5px;
}

.shop-product-image {
    width: 150px;
    background-color: rgb(255, 255, 255);
    margin-bottom: 10px;
}
.AddToCart {
    display: flex;
    flex-flow: row wrap;
    justify-content: space-around;
    align-items: center;
    border: 1px solid black;
    border-radius: 5px;
    padding: 10px;
    width: 80%;
    margin: 5px;
    z-index: 10;
    background-color: white;
}

.AddToCart > * {
    margin: 10px;
}

.add-product-image {
    width: 100px;
    height: 100px;
    background-color: rgb(255, 255, 255);

}

.AddToCart input {
    width: 40px;
    margin-right: 10px;
}

.add-cart-dimmer {
    z-index: 9;
    background-color: rgba(0, 0, 0, 0.623);
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.CartItem {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-around;
  align-items: center;
  border: 1px solid black;
  border-radius: 5px;
  padding: 10px;
  width: 80%;
  margin: 5px;
}

.CartItem > * {
  margin: 10px;
}

.cart-product-image {
  width: 100px;
  height: 100px;
  background-color: rgb(204, 204, 204);
}

.CartItem input {
  width: 40px;
  margin-right: 10px;
}

.btn_remove {
  color: black;
  background-color: white;
  border: 1px solid black;
  padding: 5px !important;
  width: auto !important;
  height: auto !important;
  font-size: 12px !important;
}

.Cart {
    display: flex;
    flex-flow: column;
    justify-content: center;
    align-items: center;
    width: 100%;
}

.cart-page-header {
    width: 90%;
    margin-bottom: 10px;
    text-align: left;
    margin-bottom: 50px;
}

.empty{
    font-size: 30px;  
}

.empty > p{
    font-size: 20px;  
}



.cart-items-container {
    display:flex;
    flex-flow: column;
    align-items: center;
    width: 100%;
}

.checkout-container {
    display:flex;
    flex-flow: column;
    justify-content: space-between;
    margin-top: 50px;
    border: 1px solid black;
    border-radius: 5px;
    padding: 10px;
    height: 90px;
    min-width: 200px;
    
}
.Checkout {
    display:flex;
    flex-flow: column;
    justify-content: center;
    align-items: center;
    width: 400px;
    height: 300px;
    border: 1px solid black;
    z-index: 11;
    background-color: white;
}

.Checkout > div {
    margin-bottom: 40px;
}


.checkout-dimmer {
    z-index: 9;
    background-color: rgba(0, 0, 0, 0.623);
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
